.AppContainer {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
  /* overflow: auto; */
}
.App {
  height: 100%;
}

.appEnter {
  top:100%;
  width: 100%;
  position: absolute;
  -webkit-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  -o-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  transition: top 0.7s cubic-bezier(.825,0,.5,1);
}

.appEnterActive {
  width: 100%;
  top:0;
  -webkit-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  -o-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  transition: top 0.7s cubic-bezier(.825,0,.5,1);
  position: absolute;
}

.appExit {
  width: 100%;
  top:0;
  -webkit-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  -o-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  transition: top 0.7s cubic-bezier(.825,0,.5,1);
  position: absolute;
}

.appExitActive {
  width: 100%;
  top:-100%;
  -webkit-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  -o-transition: top 0.7s cubic-bezier(.825,0,.5,1);
  transition: top 0.7s cubic-bezier(.825,0,.5,1);
  position: absolute;
}

.navContainer {
  z-index: 1;
  position: fixed;
  right: 0.2rem;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}




